<template>
        <vue-perfect-scrollbar class="scroll-area" v-scroll:debounce="{fn: onScroll, debounce: 200 }">
           <div class="privacy-scroller" v-html="paragraph.text"> </div>
        </vue-perfect-scrollbar>

</template>

<script>
import VuePerfectScrollbar from 'vue-perfect-scrollbar'
import _delay from 'lodash/delay'
export default {
  name: 'PrivacyScroller',
  components: {
    VuePerfectScrollbar
  },
  mounted () {
    this.paragraph.scrolled = false

    this.height = this.$el.clientHeight
    if (!this.height || this.height === 0) {
      this.height = 150
    }

    this.checkScrolled()
    this.$el.scrollTop = 1
    _delay(() => { this.inited = true }, 10)
  },
  data () {
    return {
      height: 150,
      isEnd: false,
      inited: false
    }
  },
  methods: {
    checkScrolled () {
      this.isEnd = this.$el.scrollTop + this.height + 14 >= this.$el.scrollHeight

      console.log('--- checkScrolled', this.isEnd, this.$el.scrollTop, this.height, this.$el.scrollHeight, this.paragraph.scrolled)
      // console.log(this.$el.scrollTop, this.height, this.$el.scrollHeight)

      if (this.isEnd) {
        this.$emit('scrolled', true)
        this.paragraph.scrolled = true
      } else {
        if (!this.inited) {
          this.$emit('scrolled', false)
          this.paragraph.scrolled = false
        }
      }

      return this.isEnd
    },
    onScroll () {
      this.checkScrolled()
    }
  },
  props: {
    paragraph: {type: Object},
    height_: {default: 150}
  }
}
</script>

<style lang="scss">
    .privacy-scroller {
        text-align: left;
        padding: 2px 10px;
        font-size:12px;
        min-height: 50px;
    }

    .scroll-area {
        border:1px solid silver;
        position: relative;
        margin: auto;
        width: auto;
        min-height: 50px;
    }
</style>



